\n        }\n      },\n      checkAll(checkboxValue) {\n        if (checkboxValue === 'all') {\n          this.selectedCheckboxes = [...this.checkboxes.map(item => item.value), ...this.savedInputValues];\n        }\n      }\n    }\n  };\n  </script>\n  \n  <style>\n  .listitem {\n     color: #000000;\n  }\n  \n  .create {\n     display: flex;\n     margin: auto;\n     justify-content: center;\n     align-items: center;\n     margin-left: 100px;\n     flex-direction: column;\n  }\n  \n  .radio-checkboxes{\n    display: flex;\n     margin: auto;\n     justify-content: center;\n     align-items: center;\n  }\n  </style>\n  "],"sourceRoot":""}]);
