  components: {\n      NcListItem,\n      NcActionButton,\n      NcActions,\n      Delete,\n      Pencil\n    },\n  \n    data() {\n      return {\n        input1: \"\",\n        input2: \"\",\n        input3: \"\",\n        input4: \"\",\n        items: [],\n        jsonResponse: {},\n        jsonData: [],\n\n     \n     \n      };\n    },\n  \n    created() {\n      this.items = this.$route.params.items || [];\n    },\n    \n    mounted() {\n      axios({\n          method: 'GET',\n          url:'http://192.168.21.6/index.php/apps/rides/api/0.1/get',\n          headers: {\n            'Content-Type': 'application/json',\n            'Accept-Encoding': 'application/json'\n          },\n\n\n        }).then((response) => {\n            this.jsonData = response.data\n           \n            const startingBracket = this.jsonData.indexOf(\"[\");\n            const closingBracket = this.jsonData.indexOf(\"]\");\n\n            const respondeString = this.jsonData.substring(startingBracket + 1, closingBracket);\n            const jsonResponse = JSON.parse(`[${respondeString}]`);\n            console.log(jsonResponse);\n            \n            this.jsonResponse = jsonResponse\n          })\n          .catch((error) => {\n            console.error(error);\n          });\n    },\n\n    methods: {\n      addItem() {\n\n        if (this.input1 && this.input2 && this.input3 && this.input4) {\n          const id = Date.now();\n          const newItem = {\n            id: id,\n            original: this.input1,\n            final: this.input2,\n            date: this.input3,\n            time: this.input4,\n          };\n\n          this.items.push(newItem);\n         \n        }\n      \n        const data  = {\n            id: 1,\n            original: this.input1,\n            final: this.input2,\n            date: this.input3,\n            time: this.input4,\n          };\n\n\n        axios.post('http://192.168.21.6/index.php/apps/rides/api/0.1/rides', data)\n        .then(response => {\n          console.log(response.data);\n        }) .catch(error => {\n          console.error(error);\n        });\n\n        this.input1 = \"\";\n        this.input2 = \"\";\n        this.input3 = \"\";\n        this.input4 = \"\";\n      },\n  \n      deleteItem(id) {\n\n      },\n  \n      editItem(id) {\n        const item = this.items.find(item => item.id === id);      \n      \n        if (item) {\n      this.$router.push({ \n        name: 'RideDetails', \n        params: { \n          id, \n          original: item.original, \n          final: item.final, \n          date: item.date, \n          time: item.time \n        } \n      });\n    }\n      },\n\n    \n\n     \n\n\n    },\n  };\n  </script>\n  \n  <style>\n  .listitem {\n    color: #000000;\n  }\n  \n  .create {\n    display: flex;\n    margin: auto;\n    justify-content: center;\n    flex-direction: column;\n    align-items: center;\n    margin-left: 100px;\n  }\n  </style>\n  \n\n"],"sourceRoot":""}]);
